// Posts API
export const postsAPI = {
  // Load single post
  loadPost: async (postId: string): Promise<PostResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post('/System/API/LoadPost.php', formData);
    return response.data;
  },

  // Get single post
  getPost: async (postId: string): Promise<PostResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<PostResponse>('/System/API/LoadPost.php', formData);
    return response.data;
  },

  getPosts: async (type: 'LATEST' | 'REC' | 'SUBSCRIPTIONS', startIndex?: number): Promise<PostResponse[]> => {
    return new Promise((resolve, reject) => {
      const handler = (data: WebSocketMessage) => {
        if (data.status === 'success' && data.action === 'load_posts' && Array.isArray(data.posts)) {
          //       
          const posts = data.posts.map(post => ({
            ID: String(post.id),
            AuthorID: String(post.author.id),
            Username: post.author.username,
            Name: post.author.name,
            Avatar: post.author.avatar || 'None',
            UserIcons: null, // TODO:   
            Text: post.content,
            Content: null, // TODO:   
            Date: post.date,
            Likes: post.likes,
            Dislikes: 0, //   API  
            Liked: post.liked ? 'Liked' as const : null,
            Disliked: null, //   API  
            Comments: post.comments,
            MyPost: post.my_post
          }));
          
          webSocketService.removeMessageHandler('social', handler);
          resolve(posts);
        } else if (data.status === 'error') {
          webSocketService.removeMessageHandler('social', handler);
          reject(new Error(data.message || '   '));
        }
      };

      //  
      webSocketService.addMessageHandler('social', handler);

      //  
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('social', handler);
        reject(new Error('    '));
      }, 5000);

      //     
      const request: WebSocketRequest = {
        type: 'social',
        action: 'load_posts',
        posts_type: type === 'LATEST' ? 'last' : type === 'REC' ? 'rec' : 'subscribe',
        start_index: startIndex || 0
      };
      
      console.log('Sending WebSocket request:', request);
      webSocketService.send(request).catch(error => {
        clearTimeout(timeout);
        webSocketService.removeMessageHandler('social', handler);
        reject(error);
      });
    });
  },

  getUserPosts: async (userId: string, startIndex?: number): Promise<PostResponse[]> => {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('social');
        reject(new Error('    '));
      }, 5000);

      webSocketService.addMessageHandler('social', (data: WebSocketMessage) => {
        clearTimeout(timeout);
        if (data.status === 'success' && data.action === 'load_posts' && data.posts) {
          webSocketService.removeMessageHandler('social');
          
          //       
          const posts = data.posts.map(post => ({
            ID: String(post.id),
            AuthorID: String(post.author.id),
            Username: post.author.username,
            Name: post.author.name,
            Avatar: post.author.avatar || 'None',
            UserIcons: null,
            Text: post.content,
            Content: null,
            Date: post.date,
            Likes: post.likes,
            Dislikes: 0,
            Liked: post.liked ? 'Liked' as const : null,
            Disliked: null,
            Comments: post.comments,
            MyPost: post.my_post
          }));
          
          resolve(posts);
        } else {
          webSocketService.removeMessageHandler('social');
          reject(new Error(data.message || '   '));
        }
      });

      //      
      const request: WebSocketRequest = {
        type: 'social',
        action: 'load_posts',
        posts_type: 'profile',
        target_id: userId,
        target_type: 0,
        start_index: startIndex || 0
      };
      
      webSocketService.send(request);
    });
  },

  // Post interactions (like, dislike, delete)
  interactWithPost: async (
    action: 'LIKE' | 'DISLIKE' | 'DELETE',
    postId: string
  ): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<ApiResponse>(`/System/API/PostInteraction.php?F=${action}`, formData);
    return response.data;
  },

  // Add comment to post
  addComment: async (postId: string, text: string): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);
    formData.append('Text', text);

    const response = await postsApi.post<ApiResponse>('/System/API/PostInteraction.php?F=POST_COMMENT', formData);
    return response.data;
  },

  // Get post comments
  getComments: async (postId: string): Promise<CommentResponse[]> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<CommentResponse[]>('/System/API/PostInteraction.php?F=LOAD_COMMENT', formData);
    return response.data;
  },
};
